/* Ready Steps -------------------------------------------------------------*/
.steps-outer-div{
  background-color: #fecb88;
  padding: 10vw 0vw 10vw 0vw;
  border-radius: 15vw 0% 15vw 0%;
}
.steps-hd-box .steps-hd{
  margin:0vw 0vw 3vw 0vw;
  text-align: center;
  color: #12343b;
  font-weight: bold;
  font-size: 3.8vw;
  position: relative;
}
.steps-hd-box p{
  margin: 5vw auto 7vw auto;
  width: 60%;
  color: blue;
  text-shadow: 1px 1px 10px white;
  font-size: 2vw;
  text-align: center;
  font-family: 'Poppins',sans-serif;
}
.steps-hd-box .steps-hd::before{
  content: "";
  position: absolute;
  bottom: -15px;
  left: 42%;
  height: 6%;
  width: 8%;
  background-color: blue;
}
.steps-hd-box .steps-hd::after{
  content: "";
  position: absolute;
  bottom: -15px;
  right: 42%;
  height: 6%;
  width: 8%;
  background-color: blue;
}
.steps-count-icon-div{
  overflow: hidden;
    width:8vw;
    height: 8vw;
    margin: auto;
    border-radius: 30%;
    background-color: #12343b;
/*    background-color: white;*/
    background-position: center;
    background-repeat: no-repeat;
    background-size: 2.5vw;
    border: .3vw solid #fecb88;
    outline: .2vw solid #12343b;
    transition: .4s linear;
    cursor: pointer;
}
.steps-count-icon-div:hover{
    outline: .2vw solid blue;
    transform: scale(1.2) rotate(360deg);
}
.steps-count-icon-write{
  background-image: url('../img/step_write.webp'); 
}
.steps-count-icon-temp{
  background-image: url('../img/step_temp.webp');   
}
.steps-count-icon-download{
  background-image: url('../img/step_download.webp');   
}

.steps-count-heading{
  text-align: center;
  margin: 2vw 0vw;
  font-size: 1.9vw;
  color: 12343b;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
  
  .steps-outer-div{
  background-color: #fecb88;
  border-radius: 20vw 0% 20vw 0%;
  }
  .steps-hd-box .steps-hd{
  font-size: 7.5vw;
  color: #12343b;
  margin:3vw 2vw 3vw 2vw;
  }
  .steps-hd-box p{
    margin: 5vw auto 12vw auto;
    width: 90%;
    color: blue;
    text-shadow: 1px 1px 10px white;
    font-size: 4.2vw;
    text-shadow: none;
  }
  .steps-hd-box .steps-hd::before{
    width: 0px;
  }
  .steps-hd-box .steps-hd::after{
    width: 0px;
  }
  .steps-count-icon-div {
  width: 24vw;
  height: 24vw;
  background-color: #12343b;
  background-size: 7.5vw;
  border: 1vw solid #fecb88;
  outline: .8vw solid #12343b;
  }
  .steps-count-icon-div:hover{
  outline: .8vw solid blue;
  }
  .steps-count-heading{
  font-size: 5vw;
  margin:5vw 0vw 9vw 0vw;
  color: white;
  color: #12343b;
  }
}


